import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/components/home'
import Portfolio from '@/components/portfolio'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/portfolio',
      name: 'portfolio',
      component: Portfolio
    }
  ]
})



